.cz-container {

    .cz-tool > div:first-child {
        padding-left: 0;
    }

    .cz-tool > div:last-child {
        padding-right: 0;
    }

    @media (max-width:697.8px) {
        .cz-tool > div{
            padding:0;
        }
    }

    #cz-comments{
        margin-left: -15px;
        margin-right: -15px;
    }
}

.comment-info {
    display: flex;
    flex-direction: row;
    font-family: "Microsoft YaHei","Helvetica Neue",Arial,sans-serif;

    .comment-left {
        color: #333;
        width: 100px;
        padding-top: 6px;

        @media (max-width:697.8px) {
            display: none;
        }
    }

    .comment-right {
        display: flex;
        flex-direction: column;
        flex-grow: 1;

        > .cz-uname {
            font-size: 24px;
        }

        > .comment-content-header {
            height: 25px;

            > span {
                padding-right: 2em;
                color: #aaa;
            }
        }

        > .content {
            p {
                padding: 0 0 10px;
            }
        }

        > .comment-content-footer {
            border-bottom: 1px dashed #ccc;

            .col-12 {
                display: flex;
                flex-direction: row-reverse;

                .reply-btn {
                    color: #aaa;
                    cursor: pointer;
                }
            }
        }

        > .reply-list {
            padding-left: 7px;
            border-left: 3px solid #ccc;

            .reply {
                border-bottom: 1px dashed #ccc;

                .cz-reply-name {
                    a {
                        text-decoration: none;
                    }

                    > a:first-child {
                        cursor: default;
                        color: #777;
                    }
                }

                .reply-footer {
                    margin-bottom: 10px;

                    > span {
                        padding-right: 2em;
                        color: #aaa;
                    }

                    .reply-list-btn {
                        cursor: pointer;
                    }
                }
            }
        }
    }

    .comment-content-footer {
        .replybox {
            margin-left: 15px;
            margin-right: 15px;
        }
    }

    .replybox {
        margin-top: 10px;
        margin-bottom: 10px;
        width: 100%;

        #cz-reply-editor {
            width: 100%;
            height: 120px;
        }


        .cz-tool {
            margin-left: 0;
            margin-right: 0;
            margin-top: 10px;


            .send-container {
                display: flex;
                flex-direction: row-reverse;
                align-items: center;

                .send {
                    cursor: pointer;
                }
            }
        }
    }
}
